import React from "react";
import styled, { css } from "styled-components";

import { FlexBoxContentCenter } from "../FlexBox";

const StyledHeaderTitle = styled(FlexBoxContentCenter).attrs({
  as: "h1",
})`
  ${(props) => css`
    width: 169px;
    height: 64px;
    background-color: props.theme.color.ultramarineBlue;

    color: props.theme.color.white;
    font-size: 24px;
    font-weight: 700;
  `}
`;

export default function HeaderTitle(): JSX.Element {
  return <StyledHeaderTitle>R.js</StyledHeaderTitle>;
}
